.App-header {
  position: relative;
  z-index: 3;
  height: 130px;
  padding: 20px 0px;
}

.App-title {
  text-align: center;
  font-size: 4vh;
  font-family: F25_Executive;
  /*font-variant: small-caps;*/
}

.nav-item {
  padding: 10px;
}

.nav-item > a, .nav-item > a:hover {
  font-size: 2.5vh;
  font-family: F25_Executive;
  color:#37342F;
  text-decoration: none;
}

.navbar {
  background-color: #F4E7D3;
  width: 100% !important;
}
.Footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 40px;
}

.App-footer {
  font-size: 2vh; /*1vmax*/
  font-style: italic;
}

.Rec-footer {
  background-color: red;
  color: white;
  font-size: 2.5vh; /*1.5vmax;*/
  font-weight: bold;
}

.App-footer > * {
  padding: 10px;
}

.Footer-left {
  position: fixed;
  left: 10px;
  bottom: 5px;
}

.Footer-link {
  color: #F4E7D3 !important;
  text-decoration: none;
}

.Footer-right {
  position: fixed;
  bottom: 5px;
  right: 10px;
}
.AccueilLayout {
    display: flex;
    flex-direction: column;
    text-align: center;
    
}


.AccueilLayout::after {
    content: "";
    background-image: url(/static/media/NotFound_background.d4036174.webp);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    opacity: 0.35;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
}

.AccueilLayout > * {
    padding: 10px;
    font-size: 4vh; /*3vmax*/
}

.AccueilH3 {
    padding-top: 50px !important;
    font-weight: bold !important;
    font-size: 6vh !important; /*5vmax*/
}

.AccueilBtn {
    padding: 10px;
    font-weight: bold;
    text-decoration: none;
    color: white;
    background-color: #4267B2;
    border-radius: 10px;
}

.AccueilBtn:hover{
    text-decoration: none;
    color: white;
    background-color: #3B5CA0;
}

.AccueilDev {
    font-size: 2.5vh; /*2vmax*/
}
