.TarifsLayout {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.TarifsTiles {
    flex-basis: 25%;
    flex-shrink: 2;
    margin: 10px;
    min-width: 300px;
}

.TileHeader {
    font-size: 3.5vh; /*2.2vmax;*/
    font-weight: bold;
}

.card-header, .card-text {
    font-size: 2.5vh; /*1.5vmax;*/
}

.PriceLine {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.PriceLine > * {
    flex-basis: 70%;
    flex-shrink: 1;
    flex-grow: 0;
}

.Price {
    flex-basis: 20%;
    flex-grow: 0;
}

.card-header:hover {
    cursor: pointer;
}

